import React, { useEffect, useState } from 'react';
import Typist from 'react-typist';
import 'react-typist/dist/Typist.css' // Makes cursor blink

const Start = () => {
    const [renderMsg, setRenderMsg] = useState(false);
    return (
        <div className="TypistExample">
            <Typist
                className="TypistExample-header"
                startDelay={2000}
                onTypingDone={() => setRenderMsg(true)}
                cursor={{ hideWhenDone: true }}
            >
                <a>Hello, Oumayma Lahmer !<Typist.Backspace count={16} delay={1000} />
                    worst friend ever !<Typist.Backspace count={19} delay={1000} />
                    panda !<Typist.Backspace count={7} delay={1000} />
                    fatty !<Typist.Backspace count={7} delay={1000} />
                    dummy !<Typist.Backspace count={7} delay={1000} />
                    kindest and greatest person I've ever met
                </a>
            </Typist>
            <div className="TypistExample-content">
                {renderMsg ? (
                    <Typist
                        className="TypistExample-message"
                        startDelay={2000}
                        avgTypingDelay={60}
                    >
                        So as u can see,<Typist.Delay ms={1000} /> the genius and greatest person alive,<Typist.Delay ms={1000} /> that also happens to be ur best friend just figured out how to do animations...<Typist.Delay ms={1000} /> So I thought I'll complete my gift<Typist.Delay ms={500} /> O:) <Typist.Delay ms={1000} />I know you must be very amazed and your mouth is widely open right now... :O <Typist.Delay ms={1000} />Baqe machefti waaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaalu !<Typist.Delay ms={1000} />
                        <br />
                        <br />Your best friend ever.
                    </Typist>
                ) : null}
            </div>
        </div>
    );

}
export default Start
